import Layout, { Content, Footer } from "antd/es/layout/layout";
import { Button, Card, Col, Row, Typography, Image, Dropdown, MenuProps, Empty, message } from 'antd';
import { useEffect, useRef, useState } from "react";
import "./app.less";
import { CopyOutlined, PlusOutlined, SettingOutlined } from "@ant-design/icons";
import CreateAppModal from "./components/createAppModal";
import { getApp } from "@/api/modules/app";
import H5Logo from "@/assets/svg/H5.svg"
import MPLogo from "@/assets/svg/mp.svg"
import { useNavigate } from "react-router-dom";
import copy from 'copy-to-clipboard';

const { Title, Text } = Typography;

interface ModalProps {
    showModal: () => void;
}

const View: React.FC = () => {
    const navigate = useNavigate();
    const createAppRef = useRef<ModalProps>(null);
    const [appList, setAppList] = useState([]);

    const initApp = async () => {
        const result = await getApp();
        if (result.data && Array.isArray(result.data)) {
            setAppList(result.data as []);
        }
    };

    useEffect(() => {
        initApp();
    }, [])

    const items: MenuProps['items'] = [
        {
            key: '1',
            label: '编辑'
        },
        {
            key: '2',
            label: '删除'
        },
    ];

    const navigateToApp = () => {
        navigate('/home/web');
    }

    const copyText = (e:React.MouseEvent,text:string) => {
        e.stopPropagation();
        copy(text);
        message.success('已复制')
    }

    return <Layout>
        <Content style={{ margin: '24px 32px' }}>
            <Title level={4}>应用接入</Title>
            <div className="app-header">
                <Button type="primary" icon={<PlusOutlined />} size="middle" onClick={() => createAppRef.current!.showModal()}>
                    创建应用
                </Button>
            </div>
            <div className="app-list">
                {appList.length > 0 ? <Row gutter={[16, 16]}>
                    {
                        appList.map((app: any, index) => {
                            return (<Col key={index.toString()} span={24 / 4}>
                                <Card hoverable onClick={navigateToApp}>
                                    <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between', alignItems: 'center' }}>
                                        <div style={{ display: 'flex', flexDirection: 'row' }}>
                                            <Image
                                                width={20}
                                                height={20}
                                                src={app.type === '1' ? H5Logo : MPLogo}
                                            />
                                            <Title style={{ marginLeft: '8px' }} level={5}>{app.name}</Title>
                                        </div>
                                        <Dropdown menu={{ items }} placement="bottomRight" arrow={{ pointAtCenter: true }}>
                                            <SettingOutlined />
                                        </Dropdown>
                                    </div>
                                    <div style={{marginTop:'16px'}}>
                                        <Text>AppID： <Text type="secondary">{app.uuid}</Text><CopyOutlined style={{marginLeft:'8px'}} onClick={(e)=>copyText(e,app.uuid)} /></Text>
                                    </div>
                                </Card>
                            </Col>)
                        })
                    }
                </Row> : <Empty description="暂无应用" />}
            </div>
        </Content>
        <Footer style={{ textAlign: 'center' }}>React + Ant Design</Footer>
        <CreateAppModal innerRef={createAppRef}></CreateAppModal>
    </Layout>;
}

export default View;