import { useState, useImperativeHandle, Ref } from "react";
import { Form, Input, Modal, Radio, Upload, message } from "antd";
import { PlusOutlined } from "@ant-design/icons";
import { createApp } from "@/api/modules/app";

interface Props {
	innerRef: Ref<{ showModal: () => void }>;
}

const CreateAppModal = (props: Props) => {
	const [isModalVisible, setIsModalVisible] = useState(false);
	const [form] = Form.useForm();

	useImperativeHandle(props.innerRef, () => ({
		showModal
	}));

	const showModal = () => {
		setIsModalVisible(true);
	};

	const handleOk = async () => {
		const fieldsValue = await form.validateFields();
		try {
			await createApp({
				name: fieldsValue.name,
				type: fieldsValue.type
			});
			setIsModalVisible(false);
			//清空form
			form.resetFields();
			message.success("创建成功");
		} catch (error) {

		}
	};

	const handleCancel = () => {
		//清空form
		form.resetFields();
		setIsModalVisible(false);
	};

	const normFile = (e: any) => {
		if (Array.isArray(e)) {
			return e;
		}
		return e?.fileList;
	};
	return (
		<Modal title="新建应用" open={isModalVisible} onOk={handleOk} onCancel={handleCancel} destroyOnClose={true} cancelText="取消" okText="确定">
			<Form
				labelCol={{ span: 4 }}
				wrapperCol={{ span: 14 }}
				layout="horizontal"
				form={form}
				initialValues={{ type: 1 }}
			>
				<Form.Item label="应用类型" name="type">
					<Radio.Group>
						<Radio value={1}>H5/PC/Web项目</Radio>
						<Radio value={2}>小程序</Radio>
					</Radio.Group>
				</Form.Item>
				<Form.Item label="应用名称" name="name" rules={[{ required: true }]}>
					<Input />
				</Form.Item>
				<Form.Item label="上传图标" valuePropName="fileList" getValueFromEvent={normFile}>
					<Upload action="/upload.do" listType="picture-card">
						<div>
							<PlusOutlined />
							<div style={{ marginTop: 8 }}>Upload</div>
						</div>
					</Upload>
				</Form.Item>
			</Form>
		</Modal>
	);
};
export default CreateAppModal;